/* owner.css */

.owner-page {
  font-family: 'Poppins', sans-serif;
  background-color: #f2f2f2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.owner-page h2 {
  color: #333;
  text-align: center;
  margin-bottom: 2rem;
}

.owner-page section {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  margin-bottom: 2rem;
}

.owner-page section h3 {
  color: #333;
  margin-bottom: 1rem;
}

.owner-page input,
.owner-page button {
  padding: 0.8rem 1.2rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.owner-page input {
  background-color: #f2f2f2;
  border: 1px solid #ddd;
}

.owner-page button {
  background-color: #333;
  color: #fff;
  transition: background-color 0.3s ease-in-out;
}

.owner-page button:hover {
  background-color: #555;
}

.owner-page ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.owner-page ul li {
  margin-bottom: 1rem;
}

.owner-page .low-inventory {
  color: #f00;
  font-weight: bold;
}