.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #4CAF50;
    color: #fff;
  }
  
  .logo {
    font-size: 1.5em;
    font-weight: bold;
  }
  
  .nav {
    display: flex;
    gap: 15px;
  }
  
  .link{
    color: #fff;
    text-decoration: none;
    font-size: 1em;
    font-weight: 500;
  }
    
  .cart-icon {
    color: #fff;
    cursor: pointer;
  }

  .logo{
    border-radius: 50%;
    border: 1px solid brown;
    width: 100px;
    height: 100px;
    
  }
  .logo-div{
    padding-left: 50px;
  }
  