/* Body Styles */
.body {
  padding: 20px;
  text-align: center;
  background-color: #f2f2f2;
}

.body h2 {
  font-size: 2.2em;
  color: #444;
  margin-bottom: 25px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.card {
  background: linear-gradient(135deg, #fdfbfb 0%, #ebedee 100%);
  border: none;
  border-radius: 15px;
  width: 240px;
  text-align: center;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.4s, box-shadow 0.4s;
}

.card:hover {
  transform: scale(1.06);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.card-image {
  width: 100%;
  height: 220px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  object-fit: cover;
}

.card-content {
  padding: 20px;
}

.card-title {
  font-size: 1.3em;
  color: #333;
  margin: 12px 0;
  font-weight: 600;
}

.card-price {
  color: #3cb371;
  font-size: 1.2em;
  font-weight: 700;
  margin: 8px 0;
}

.card-description {
  color: #777;
  font-size: 0.95em;
  margin-bottom: 18px;
}

/* Button Styles */
.card-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
}

.cart-button, .buy-button {
  padding: 10px 15px;
  font-size: 0.9em;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.cart-button {
  background: linear-gradient(135deg, #42a5f5, #1e88e5);
  color: white;
}

.buy-button {
  background: linear-gradient(135deg, #ff8a65, #f4511e);
  color: white;
}

.cart-button:hover {
  background: linear-gradient(135deg, #1e88e5, #42a5f5);
  transform: scale(1.05);
}

.buy-button:hover {
  background: linear-gradient(135deg, #f4511e, #ff8a65);
  transform: scale(1.05);
}

.search-cart {
  display: flex;
  align-items: center;
  gap: 12px;
  padding-bottom: 35px;
}

.search-input {
  padding: 8px;
  font-size: 1em;
  border-radius: 50px;
  border: 1px solid #ddd;
}
