.register-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    
  }
  .container{
    padding: 10%;
    background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT-e1iXnvvJGBIfSP4BwbgbVbxXixXtd4xqJg&s');
    background-repeat: no-repeat;
    background-size: cover;
  }
  .account{
    text-align: center;
    padding-top: 10px;
  }
  
  .register-title {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  .register-form .form-group {
    margin-bottom: 15px;
  }
  
  .register-form label {
    display: block;
    margin-bottom: 5px;
    color: #555;
  }
  
  .register-form input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .register-button {
    width: 100%;
    padding: 10px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .register-button:hover {
    background-color: #45a049;
  }
  
  .error-message {
    color: red;
    margin-bottom: 15px;
    font-size: 14px;
  }
  /* Styling for the role select dropdown */
.role-select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  background-color: #fff;
  color: #333;
  appearance: none;
  cursor: pointer;
  transition: border-color 0.3s;
}

.role-select:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 4px rgba(0, 123, 255, 0.2);
}

.role-select option {
  color: #333;
  padding: 10px;
}
