/* Footer Styles */
.footer {
    display: flex;
    justify-content: space-around;
    padding: 30px;
    background-color: #35424a;
    color: #f2f2f2;
    text-align: left;
    flex-wrap: wrap;
  }
  
  .footer-info, .footer-links, .footer-policy, .footer-social {
    max-width: 200px;
    margin: 10px;
  }
  
  .footer-info h3,
  .footer-links h3,
  .footer-policy h3,
  .footer-social h3 {
    font-size: 1.3em;
    color: #76c7c0;
    margin-bottom: 10px;
  }
  
  .footer-info p {
    font-size: 0.9em;
    margin: 5px 0;
  }
  
  .footer-links ul, 
  .footer-policy ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-links ul li, 
  .footer-policy ul li {
    margin: 5px 0;
  }
  
  .footer-links ul li a, 
  .footer-policy ul li a {
    color: #f2f2f2;
    text-decoration: none;
    font-size: 0.9em;
  }
  
  .footer-links ul li a:hover, 
  .footer-policy ul li a:hover {
    text-decoration: underline;
  }
  
  .footer-social .social-icons {
    display: flex;
    gap: 10px;
    margin-top: 10px;
  }
  
  .footer-social .payment-icons {
    display: flex;
    gap: 10px;
    margin-top: 10px;
    font-size: 1.5em;
    color: #f2f2f2;
  }
  
  .footer-social .social-icons a {
    color: #f2f2f2;
    font-size: 1.2em;
    transition: color 0.3s;
  }
  
  .footer-social .social-icons a:hover {
    color: #76c7c0;
  }
  
  .footer-social .payment-icons svg:hover {
    color: #76c7c0;
  }
  